import Vue from 'vue'
import {requestDeleteParameter} from '../../../../../ajax/index'
export const deleteFun =({attrName,_id,attrSel}) =>{
  return new Promise(async (resolve, reject) =>{
    const result = await Vue.prototype.$confirm(`(${attrName}), ?`, '', {
      confirmButtonText: '',
      cancelButtonText: '',
      type: 'warning'
    }).catch(e =>console.log(e))
    if(result === 'confirm'){
      const result = await requestDeleteParameter({_id,attrSel})
      console.log('confirm',result)
      if(result.code === 0){
        resolve(true)
      }else{
        reject(false)
      }
    }else {
      Vue.prototype.$message({type: 'info',message: ''})
    }
  })
}