    updated() { // 
      
    },
    beforeDestroy() { // 
      
    },
    destroyed() { // 
      
    },
    computed: { // 
      
    },
    watch: { // 
      
    },
    */
    methods: { // 
      casChange(value){
        console.log(value)
        if(value[2]){ // 
          this.$store.dispatch('getChangeCasValue',{value})
          this.$store.dispatch('getRequestParameter',{categoryId:value[2],attrSel:this.activeName})
        }else{
          this.$store.dispatch('getChangeCasValue',{value:[]})
        }
      },
    },
    components: { // 

    }
  }
</script>
